/* src/components/NewsTicker.css */

.ticker-container {
    background-color: #4caf50; /* Background color for the ticker */
    color: white; /* Text color */
    overflow: hidden; /* Hide overflowing content */
    white-space: nowrap; /* Prevent line breaks */
    position: relative; /* Position for absolute children */
    padding: 10px; /* Padding around the ticker */
  }
  
  .ticker {
    display: inline-block; /* Align items in a single line */
    animation: ticker-animation 30s linear infinite; /* Animation effect */
  }
  
  .ticker-item {
    display: inline; /* Display items inline */
    padding-right: 50px; /* Space between news items */
    font-size: 18px; /* Font size for news items */
  }
  
  @keyframes ticker-animation {
    0% {
      transform: translateX(100%); /* Start from right */
    }
    100% {
      transform: translateX(-100%); /* Move to left */
    }
  }
  