/* src/components/Home.css */
.home-container {
    padding: 20px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 10px;
    margin: 20px auto;
    max-width: 800px;
  }
  
  h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  h2 {
    font-size: 2rem;
    margin: 20px 0 10px;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  blockquote {
    font-style: italic;
    margin: 20px;
    padding: 10px;
    border-left: 4px solid #00796b;
    background-color: #f9f9f9;
  }
  
  .team-member {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #00796b;
    border-radius: 5px;
    background-color: #e0f2f1;
  }
  